/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core'

export default function Features(props) {
    const app = props.app
    const features = app.features

    let list = features.map((feature, index) => {
        const aosList = [
            "slide-up",
            "flip-left",
            "zoom-in",
            "flip-right"
        ]

        const texts = feature.texts.map((text) => {
            return (
                <span css={css`
                    diplay: inline-block;
                    margin: 6px;
                    color: #4E4E4E;
                    font-weight: 100;
                `}
                    key={text}
                >
                    {text}
                </span>
            )
        })

        const Picture = () => {
            const shadowCss = feature.shadow ? css`
                border-radius: 5px;
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            ` : css``

            const aos = aosList[index % aosList.length]

            return (
                <div css={css`
                    width: 40vw;
                    @media (max-width: 500px) {
                        width: 98vw;
                    }
                `} data-aos={aos}>
                    <div css={shadowCss}>
                        <img css={css`
                        max-width: 100%;
                        height: auto;
                    `}
                            src={require(`../apps/${app.slug}/2.png`)}
                        />
                    </div>
                </div>
            )
        }

        const Content = () => (
            <div css={css`
                display: flex;
                flex-direction: column;
            `} data-aoes="fade-up">
                <div css={css`
                    display: flex;
                    align-items: center;
                    margin-bottom: 10px;
                `}>
                    <i className='material-icons-round'>{feature.titleIcon}</i>
                    <span css={css`
                        font-style: black;
                        font-size: 20px;
                        margin-left: 5px;
                    `}>{feature.title}</span>
                </div>
                {texts}
            </div>
        )

        const Left = (index % 2 == 0) ? <Picture /> : <Content />
        const Right = (index % 2 == 0) ? <Content /> : <Picture />

        return (
            <div css={css`
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                justify-content: center;
                margin: 0 auto;
                align-items: center;
                margin-top: 10vh;
                margin-bottom: 10vh;
                min-height: 50vh;
            `}
                key={index}
            >
                {Left}
                <div css={css`
                    width: 5vw;
                `} />
                {Right}
            </div>
        )
    })

    return (
        <div css={css`
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
        `}>
            <div css={css`
                margin: 0 auto;
            `}>
                <p css={css`
                    font-style: bold;
                    font-weight: 900;
                    font-size: 2em;
                `}>Features</p>
            </div>
            {list}
        </div>
    )
}