/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core'

export default function ProductHunt(props) {
    const app = props.app
    if (app.productHunt === undefined) {
        return (
            <div />
        )
    }

    const Left = () => (
        <div css={css`

        `}>
            <iframe css={css`
                border: none;
                width: 40vw;
                height: 60vh;
                @media (max-width: 420px) {
                    width: 98vw;
                }
            `}
                src={`https://cards.producthunt.com/cards/posts/${app.productHunt.id}?v=1`}
                frameBorder="0"
                scrolling="no"
                allowFullScreen
            >
            </iframe>
        </div>
    )

    const Right = () => (
        <div css={css`

        `}>
            <p css={css`
                font-size: 1.5em;
                font-style: bold;
                font-weight: 900;
            `}>Featured on</p>
            <a target="_blank"
                href={app.productHunt.link}>
                <img src={`https://api.producthunt.com/widgets/embed-image/v1/featured.svg?post_id=${app.productHunt.id}&theme=light`} />
            </a>
        </div>
    )

    return (
        <div css={css`

        `}>
            <div css={css`
                display: flex;
                flex-direction: row;
                justify-content: center;
                flex-wrap: wrap;
                margin: 0 auto;
                padding: 30px 0px;
                margin-top: 50px;    
            `}>
                <Left />
                <div css={css`
                    width: 10vh;
                `}/>
                <Right />
            </div>
        </div>
    )
}