/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core'

function DownloadLinks(props) {
    const app = props.app
    const download = app.download

    let links = []

    const Appstore = (platform) => (
        <div css={css`
            display: flex;
            flex-direction: column;
        `} key={platform.id}>
            <a css={css`
                display: inline-block;
                overflow: hidden;
                width: 200px;
                height: 50px;
                background: url('/images/download macos.svg') no-repeat;
            `}
                href={`https://apps.apple.com/app/${platform.id}`}
            />
            <span css={css`
                font-size: 12px;
                margin-top: -8px
            `}>
                {`Require ${platform.platform} ${platform.version}`}
            </span>
        </div>
    )

    const Trial = () => (
        <a css={css`
                background-color: red;
                margin: auto;
                position: relative;
                color: white;
                font-size: 14px;
                padding: 6px 26px;
                border-radius: 5px;
                background-color: #6c7a89;
                box-shadow: 0 4px #6f6f6f;
                border: none;
                white-space: normal;
                top: -10px;
            `}
            href='mailto:onmyway133@gmail.com'
            title='Contact support for free trial'
            key="trial"
        >
            Free Trial
        </a>
    )

    if (download.appstore !== undefined) {
        download.appstore.forEach((platform) => {
            const link = Appstore(platform)
            links.push(link)
        })
    }

    links.push(Trial())

    return (
        <div css={css`
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;

            a {
                text-decoration: none;
            }

            a:active {
                position: relative;
                top: 1px;
            }

            a:hover {
                transform: scale(1.1);
            }
        `}>
            {links}
        </div>
    )
}

export default function Download(props) {
    const app = props.app

    return (
        <div css={css`
            border-radius: 5px;
            background: linear-gradient(-45deg, #F9BF3B, #23a6d5, #23d5ab);
	        background-size: 400% 400%;
            animation: gradient 15s ease infinite;
            
            @keyframes gradient {
                0% {
                    background-position: 0% 50%;
                }
                50% {
                    background-position: 100% 50%;
                }
                100% {
                    background-position: 0% 50%;
                }
            }
        `}>
            <div css={css`
                display: flex;
                flex-direction: column;
                align-items: center;
                margin: 0 auto;
                padding: 30px 0px;
            `}>
                <p css={css`
                    font-size: 2em;
                    text-align: center;
                    font-style: bold;
                    font-weight: 900;
                    margin: 0;
                `}>Download</p>
                <p css={css`
                    font-size: 1.2em;
                `}>{`See ${app.name} in action`}</p>
                <p css={css`
                    text-align: center;
                    width: 54%;
                    color: #3E3E3E;
                    font-style: italic;
                    margin: 0;
                `}>{app.headline.text}</p>
                <div css={css`
                    margin-top: 40px;
                `}>
                    <DownloadLinks app={props.app} />
                </div>
            </div>
        </div>
    )
}