/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core'
import Wave from '../lib/react-wavify'

export default function Header(props) {
    const app = props.app
    const backgroundColor = app.header.background 

    const Background = () => (
        <div
            css={css`
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
            `}
        >
            <div
                css={css`
                    background-color: ${backgroundColor};
                    height: 600px;
                    margin-bottom: -20px;
                `}
            />
            <div
                css={css`
                    transform: rotateX(180deg);
                `}>
                <Wave
                    fill={backgroundColor}
                    options={{
                        height: 10,
                        amplitude: 20,
                        speed: 0.2,
                        points: 4
                    }}
                />
            </div>
        </div>
    )

    const Navigation = () => (
        <div css={css`
            display: flex;
            align-items: center;
            a {
                text-decoration: none;
                margin-left: 10px;
                font-size: 18px;
                font-weight: 600;
                padding: 8px 8px;
            }

            a:hover {
                border-radius: 8px;
                background-color: rgba(50, 50, 50, 0.1);
                color: #3F3F3F;
            }
        `}>
            <a
                href='#features'
                onClick={() => {
                    const options = {
                        behavior: 'smooth'
                    }
                    document.getElementById('features-section').scrollIntoView(options)
                }}
            >
                Features
            </a>
            <div css={css`
                padding: 10px;
                border-radius: 10px;
                background-color: '#F6D456';
            `}>
                <a
                    href="#download"
                    onClick={() => {
                        const options = {
                            behavior: 'smooth'
                        }
                        document.getElementById('download-section').scrollIntoView(options)
                    }}
                >
                    Download
                </a>
            </div>
        </div>
    )

    const Logo = () => (
        <div css={css`
            display: flex;
            justify-content: center;
            align-items: center;
        `}>
            <img css={css`
                width: 50px;
                height: auto;
            `}
                src={require(`../apps/${app.slug}/icon.png`)}
            />
            <span css={css`
                font-size: 1.4em;
                margin-left: 10px;
                font-weight: bold;
                user-select: none;
            `}>
                {app.name}
            </span>
        </div>
    )

    return (
        <div css={css`
            position: relative;
        `}>
            <Background />
            <div css={css`
                position: absolute;
                top: 2vh;
                left: 0;
                width: 100%;
                height: 50%;
            `}>
                <div css={css`
                    display: flex;
                    justify-content: space-around;
                    align-items: center;
                    flex-wrap: wrap;
                `}>
                    <Logo />
                    <Navigation />
                </div>
            </div>
        </div>
    )
}